import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { ASSETS_CONFIG } from '../../../../../config/api.config';
import { DeliveryDto } from '../../../../shared/interfaces/delivery/delivery.dto';

@Injectable({
  providedIn: 'root'
})
export class DeliveryService {

  constructor(
    private readonly http: HttpClient,
  ) { }

  list(): Observable<DeliveryDto[]> {
    return this.http.get<DeliveryDto[]>(`${ASSETS_CONFIG.baseURL}/delivery.json`)
  }
}
