import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ComponentRef, ComponentProps } from '@ionic/core';
import { ModalController } from '@ionic/angular';

import { PreferencesService } from '../../shared/services/preferences/preferences.service';
import { SkillsService } from '../../shared/services/mocks/skills/skills.service';
import { SkillDto } from '../../shared/interfaces/skills/skills.dto';
import { CertificationsService } from '../../shared/services/mocks/certifications/certifications.service';
import { CertificationDto } from '../../shared/interfaces/certifications/certification.dto';
import { ExternalRedirect } from '../../shared/utils/ExternalLink';
import { ProductsService } from '../../shared/services/mocks/products/products.service';
import { ProductDto } from '../../shared/interfaces/products/product.dto';
import { DeliveryService } from '../../shared/services/mocks/delivery/delivery.service';
import { DeliveryDto } from '../../shared/interfaces/delivery/delivery.dto';
import { ImageShowComponent } from '../../shared/components/utils/image-show/image-show.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit, OnDestroy {

  heroImg: string;
  skills: SkillDto[] = [];
  certifications: CertificationDto[] = [];
  products: ProductDto[] = [];
  deliveries: DeliveryDto[] = [];

  private subscriptions = new Subscription();

  constructor(
    private readonly route: Router,
    private readonly preferencesService: PreferencesService,
    private readonly skillsService: SkillsService,
    private readonly certificationsService: CertificationsService,
    private readonly productsService: ProductsService,
    private readonly deliveryService: DeliveryService,
    private readonly modalCtrl: ModalController,
  ) { }
  // Fazer o formulario e acertar os botoes para as proximas paginas
  ngOnInit() {
    const theme = this.preferencesService.getThemePreference()
    this.heroImg = `assets/imgs/fotos/my-github-${theme}.png`;
    this.changeHeroImg();
    // this.loadSkills();
    // this.loadCertifications();
    this.loadProducts();
    this.loadDelivery();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadSkills() {
    this.skillsService.list()
      .subscribe(data => {
        this.skills = data;
      });
  }

  loadCertifications() {
    this.certificationsService.list()
      .subscribe(data => {
        this.certifications = data;
      });
  }

  loadProducts() {
    this.productsService.list()
      .subscribe(data => {
        this.products = data;
      });
  }

  loadDelivery() {
    this.deliveryService.list()
      .subscribe(data => {
        this.deliveries = data;
      });
  }

  openLink(url: string) {
    ExternalRedirect.externalLinkHandle(url);
  }

  ionViewWillEnter() {

  }

  changeHeroImg() {
    this.preferencesService.themeSubscriber.subscribe(theme => {
      this.heroImg = `assets/imgs/fotos/my-github-${theme}.png`;
      console.log(this.heroImg);
    });
  }

  openCardapio() {
    this.showModal(ImageShowComponent);
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modalCtrl.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-100',
      componentProps,
      
    });

    return await modal.present();
  }


}